@charset "UTF-8";
.ly-homeIco {
  text-align: center; }
  .ly-homeIco_image {
    width: 50%;
    max-width: 160px;
    margin: 0 auto 24px; }

.svg-logoMark {
  position: relative;
  fill: #65cee9;
  width: 75%;
  opacity: 0.3;
  filter: drop-shadow(0 0px 3px rgba(0, 0, 0, 0.3));
  animation-duration: 1s;
  animation-name: slideIn;
  left: -100px; }

@keyframes slideIn {
  from {
    left: -200px; }
  50% { }
  to {
    left: -100px; } }

.bl-summary {
  overflow: hidden;
  padding-bottom: 16px;
  padding-top: 24px;
  background: #eff1f3;
  min-height: 320px;
  animation-duration: 3s;
  animation-name: fadeIn; }
  .bl-summary_ttl {
    font-size: 1.9rem;
    margin-bottom: 20px; }
  .bl-summary_txt {
    font-size: 1.4rem;
    margin-left: 2%; }

@keyframes fadeIn {
  from {
    background: #fff;
    color: #fff; }
  to {
    background: #eff1f3;
    color: #181818; } }

.bl-recentCnt {
  border-bottom: 1px solid #181818;
  padding-bottom: 16px;
  margin-top: 64px; }
  .bl-recentCnt_ttl {
    margin: 0 0 16px;
    font-size: 1.8rem;
    text-align: center;
    color: #20bedd; }
  .bl-recentCnt_pages {
    list-style: none;
    margin: 0 0 32px 2%;
    padding: 0; }
  .bl-recentCnt_list {
    width: 100%;
    border-bottom: 1px dashed #d4d9e6; }
    .bl-recentCnt_list:last-of-type {
      border: none; }
  .bl-recentCnt_link {
    color: #181818;
    padding: 12px 4px;
    display: block; }
  .bl-recentCnt_pagettl {
    font-size: 1.5rem;
    margin: 0 0 2px; }
  .bl-recentCnt_tag {
    display: flex;
    list-style: none;
    padding: 0;
    margin-bottom: 2px; }
    .bl-recentCnt_tag_list {
      padding: 4px 8px;
      border-radius: 40px;
      background: #eef7fb;
      color: #888;
      font-size: 1.3rem;
      line-height: 1; }
  .bl-recentCnt_updateDate {
    font-size: 1.3rem;
    color: #888;
    margin: 0; }

.bl-pageNav {
  padding-top: 64px;
  padding-bottom: 64px; }
  .bl-pageNav_list {
    list-style: none;
    padding: 0;
    margin: 0;
    background: #fcfcfc; }
  .bl-pageNav_cnt {
    width: 100%;
    padding: 8px 16px;
    border-bottom: 1px solid #d4d9e6;
    position: relative; }
    .bl-pageNav_cnt:after {
      content: "";
      position: absolute;
      display: inline-block;
      width: 8px;
      height: 8px;
      right: 16px;
      bottom: 20px;
      border-bottom: 1px solid #888;
      border-right: 1px solid #888;
      transform: rotate(45deg);
      transition: 0.4s; }
    .bl-pageNav_cnt.is-active:after {
      transform: rotate(-135deg); }
    .bl-pageNav_cnt.not-focus h3 {
      color: #d8d8d8; }
    .bl-pageNav_cnt.not-focus .bl-pageNav_cntNumber {
      color: #d8d8d8; }
  .bl-pageNav_index {
    display: block;
    color: #181818; }
  .bl-pageNav_ttl {
    display: flex;
    align-items: baseline; }
    .bl-pageNav_ttl h3 {
      font-size: 1.5rem;
      font-weight: normal;
      padding-right: 12px;
      margin: 0;
      color: #181818;
      transition: 0.4s; }
  .bl-pageNav_cntNumber {
    font-size: 1rem;
    align-items: center;
    background: #eee;
    width: 18px;
    text-align: center;
    height: 18px;
    line-height: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 1px;
    color: #666;
    transition: 0.4s; }
  .bl-pageNav_txt {
    font-size: 1.5rem;
    margin-bottom: 0; }
  .bl-pageNav_menu {
    line-height: 0;
    height: 0;
    overflow: hidden;
    opacity: 0.3;
    transition: 0.4s;
    padding: 0 0 0 5%; }
    .bl-pageNav_menu.is-open {
      line-height: normal;
      /* numberに書き換える*/
      height: auto;
      opacity: 1;
      padding: 4px 0 4px 5%; }
      .bl-pageNav_menu.is-open ul li {
        padding: 8px;
        border-bottom: 1px solid #d4d9e6; }
        .bl-pageNav_menu.is-open ul li:last-of-type {
          border-bottom: none; }
  .bl-pageNav_menuList {
    list-style: none;
    padding-left: 0; }
  .bl-pageNav_link {
    color: #181818;
    font-size: 1.4rem; }
